import Vue from 'vue'
import Router from 'vue-router'
// 
import Index from '@/page/Index/Index.vue'
import Searched from '@/page/Searched/Searched.vue'
import Model from '@/page/Model/Model.vue'
import WantedMain from '@/page/Wanted/WantedMain.vue'
import Discussion from '@/page/Discussion/Discussion.vue'
import Personal from '@/page/Personal/Personal.vue'
import WantedDetail from '@/page/Wanted/WantedDetail.vue'
import FileDetail from '@/page/Searched/FileDetail.vue'
import DiscDetail from '@/page/Discussion/DiscussionDetail.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta: {
        title: ''
      }
    },
    {
      //  query 
      path: '/search',
      name: 'Searched',
      component: Searched,
      meta: {
        title: '-TWT'
      }
    },
    {
      //  query 
      path: '/mainpage',
      name: 'Model',
      component: Model
      //  title  Model.vue 
    },
    {
      //  query 
      path: '/filterbytag',
      name: 'Tag',
      component: Model
      //  title  Model.vue 
    },
    {
      //  query 
      path: '/wanted',
      name: 'Wanted',
      component: WantedMain,
      meta: {
        title: '-TWT'
      }
    },
    {
      path: '/wanteddetail',
      name: 'WantedDetail',
      component: WantedDetail,
      meta: {
        title: '-TWT'
      }
    },
    {
      path: '/discussion',
      name: 'Discussion',
      component: Discussion,
      meta: {
        title: '-TWT'
      }
    },
    {
      path: '/user',
      name: 'Personal',
      component: Personal,
      meta: {
        title: '-TWT'
      }
    },
    {
      path: '/file',
      name: 'FileDetail',
      component: FileDetail,
      meta: {
        title: '-TWT'
      }
    },
    {
      path: '/discdetail',
      name: 'DiscDetail',
      component: DiscDetail,
      meta: {
        title: '-TWT'
      }
    }
  ]
})



